html {
  font-size: 16px; /* Default base size */
}

.logo-display {
  position: fixed;
  top: 0.625rem;
  left: 0.625rem;
  width: 12.5rem;
  height: auto;
}

@media (max-width: 768px) {
  html {
    font-size: 14px; /* Smaller base size on mobile */
  }

  .logo-display {
    top: auto;
    translate: calc(-55%);
    margin-left: 50vw;
    bottom: 1rem; /* Move to bottom on mobile */
    width: 10rem;     /* Adjust width for smaller screens */
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 2.5rem;
  }
}

@media (min-width: 1025px) {
  html {
    font-size: 18px; /* Larger base size on desktops */
  }
}

body {
  font-size: 1rem; /* Scales with the root font size */
}

h1 {
  font-size: 2rem;
}

p {
  font-size: 1rem;
}

.fade-in {
  opacity: 0;                 /* Initial state is transparent */
  transition: opacity 0.75s;  /* Transition effect for opacity */
}

.fade-in.visible {
  opacity: 1;                 /* Final state is fully visible */
}